import { useState, useRef, useEffect, useContext } from "react";

import CommentIcon from "../res/svg/commentIcon.svg"
import { Vote } from "./Vote";
import { Timestamp } from "./Timestamp"

import s from "../style/post.module.sass";

import { CommentSection } from "./CommentSection";
import { DropdownItem } from '../components/MultilevelMenu';

//markdown styling
import mdStyle from "../style/document.module.sass";
import markdown from "../helpers/markdown";
const md = markdown({ anchor: false, embeds: true, supressHeadings: true });

import { MenuData } from "./layout/ContextMenu";
import { useIdentity } from "../hooks/identity";

import { FaBan, FaLock, FaLockOpen, FaEye, FaEyeSlash, FaCommentDots, FaCopy } from "react-icons/fa";
import { HiOutlineClipboardCopy, HiClipboardCopy } from "react-icons/hi";
import { ImEmbed } from "react-icons/im";
import { MdEdit } from "react-icons/md";
import { ConfirmMenuItem } from "./ConfirmMenuItem";
import { PostForm } from "./PostForm";
import { apiUrl } from "../constants";
import { FallbackImg } from "./FallbackImg";
import {DialogBox, Modal} from "./layout/Modal"
import {getRandom} from "../constants/randomText";

console.log(s.postScronch)

export const Post = (props) => {
    var p = props.post;
    var [commentsOpen, setCommentsOpen] = useState(false);
    

    var [deletion, setDeletion] = useState(false);
    var delRef = useRef();
    var [highlighted, setHightlighted] = useState(false);

    //----show a context menu on rightclick
    var contextMenu = useContext(MenuData);
    var [id, axios] = useIdentity();
    var embed = props.embed;

    var [editing, setEditing] = useState(false);

    var [comState, setComState] = useState(p.entity.comments_hidden ? "hidden" : p.entity.comments_locked ? "locked" : undefined);
    useEffect(() => {
        if (p.entity.comments_hidden) {
            setComState("hidden");
        } else if (p.entity.comments_locked) {
            setComState("locked");
        } else {
            setComState("e");
        }
    }, [p.entity.comments_locked, p.entity.comments_hidden])

    switch (comState) {
        case "locked": 
            p.entity.comments_hidden = false; 
            p.entity.comments_locked = true; 
        break;
        case "hidden":  
            p.entity.comments_hidden = true; 
        break;
        default: 
            p.entity.comments_hidden = false; 
            p.entity.comments_locked = false;
    }

    const postRef = useRef();

    if(props.rerender === p.entity.id) {
        scrollFetch();
    }

    function context(evt) {
        if (deletion || editing) return false
        evt.preventDefault();


        var menu = [
            <ConfirmMenuItem text="Embed" leftIcon={<ImEmbed />} onClick={async () => {
                try {
                    await navigator.clipboard.writeText(`<iframe src="https://drkslv.github.io/discussion/embed/post?id=${p.entity.id}" width="700px" height="500px" frameborder="0"></iframe>`)
                    return true;
                } catch (error) {
                    return false;
                }
            }} />,
            <ConfirmMenuItem text="Copy markdown" leftIcon={<FaCopy/>} onClick={async () => {
                try {
                    await navigator.clipboard.writeText(p.text)
                    return true;
                } catch (error) {
                    return false;
                }
            }} />
        ]
        if (typeof id.active !== "number" || !id.users[id.active])
            return contextMenu.openMenu(evt.pageX, evt.pageY, menu);

        if (p.author && id.users[id.active].username === p.author.username)
            menu.push(
                <DropdownItem text="Edit" onClick={edit} leftIcon={<MdEdit />} />);
        if ((p.author && id.users[id.active].username === p.author.username) || id.users[id.active].moderator)
            menu.push(<DropdownItem text="Delete" onClick={deleteModal} warn={true} leftIcon={<FaBan />} />);
        if (id.users[id.active].moderator) {
            var l = p.entity.comments_locked;
            var h = p.entity.comments_hidden;
            menu.push(
                <DropdownItem text="Comments" warn={true} leftIcon={<FaCommentDots />}>
                    <DropdownItem text={!l ? "Lock" : "Unlock"} leftIcon={!l ? <FaLock /> : <FaLockOpen />} warn={true} onClick={() => entity(!l)} />
                    <DropdownItem text={!h ? "Hide" : "Show"} leftIcon={!h ? <FaEyeSlash /> : <FaEye />} warn={true} onClick={() => entity(undefined, !h)} />
                </DropdownItem>
            )
        }

        contextMenu.openMenu(evt.pageX, evt.pageY, menu);

    }

    async function entity(lock, hide) {
        axios.put(`/entity/${p.entity.id}`, {
            comments_locked: typeof lock === "boolean" ? String(lock) : undefined,
            comments_hidden: typeof hide === "boolean" ? String(hide) : undefined
        }).then((res) => {
            contextMenu.removeMenu();
            setCommentsOpen(false);

            if (res.data.comments_hidden === true) {
                setComState("hidden");
            } else if (res.data.comments_locked === true) {
                setComState("locked");
            } else {
                setComState("e");
            }
        });
    }

    async function edit() {
        console.log("edit post");

        contextMenu.removeMenu()
        setEditing(true);
        scrollFetch();
    }

    async function deleteP() {
        setHightlighted(false);
        axios.delete(`/post/${p.entity.id}`).then(async () => {
            console.log("scronch post");

            contextMenu.removeMenu()
            setCommentsOpen(false);
            setDeletion(true);
            scrollFetch();
            await later(2000);
            props.rerenderFunc();
        }).catch(() => {
            contextMenu.removeMenu()
        });
    }

    function scrollFetch() {
        var bodyRect = document.body.getBoundingClientRect(),
            elemRect = postRef.current.getBoundingClientRect(),
            offset = elemRect.top - bodyRect.top;

        if (postRef.current.clientHeight > 350 && commentsOpen && offset < window.scrollY && !embed) {
            postRef.current.scrollIntoView();
            window.scrollBy(0, -120);
        }
    }

    async function onEdit(text, cancel) {
        if (cancel) return setEditing(false);

        try {
            var res = axios.put(`/post/${p.entity.id}`, { text });
        } catch (error) {
            return false
        }
        setEditing(false);
        props.rerenderFunc(p.entity.id);
        return true;
    }

    function deleteModal() {
        setHightlighted(true);
        delRef.current.toggleModal();
    }
    function deleteCancel() {
        setHightlighted(false);
    }

    //----Expand when clicked

    return (
        <><DialogBox onConfirm={deleteP} onCancel={deleteCancel} ref={delRef} text={"Delete this post?"}></DialogBox>
        <div style={{zIndex:highlighted?6:undefined}}
        className={`${s.postWrapper} ${deletion && !(commentsOpen) && s.postScronch}`}>
            
            <div className={`${s.post}`}
                ref={postRef}
                onClick={() => {
                    if (p.entity.comments_hidden) {
                        postRef.current.classList.remove(s.wrong)
                        void postRef.current.offsetWidth;
                        postRef.current.classList.add(s.wrong)
                    }
                    if (!deletion && !editing) setCommentsOpen(!commentsOpen);
                    if (!editing) scrollFetch();
                }}
                onContextMenu={context}
                data-expanded={commentsOpen}
            >
                {!editing ? (<PostContent post={p} expanded={commentsOpen}/>)
                    : (<PostForm title={p.title} text={p.text} edit onEdit={onEdit} />)
                }
            </div>
            {!p.entity.comments_hidden ? <CommentSection id={p.entity.id} open={commentsOpen} key={"cs" + p.entity.id} entity={p.entity} /> : <></>}
        </div>
        </>
    );
};

export function PostContent({post: p, expanded}) {
    // determine avatar
    var avatar; var text = p.text; 
    if (p.author) {
        avatar = p.author.avatarUrl;
    } else {
        if (p.text === "~d~") {
            avatar=apiUrl+"/assets/img/trash.jpg";
            text = getRandom("post deleted")
        } else if (p.text === "~r~") {
            avatar=apiUrl+"/assets/img/nuke.jpg"
            text = getRandom("post removed")
        } else {
            avatar=apiUrl+"/assets/img/kill.png"
        }
    }

    var smR = window.screen.width<600;

    // determine vote
    var myVote = null;
    var votes = p.entity.votes;
    if (votes && votes[0])
        myVote = votes[0].positive;

    // determine time it takes to expand
    const bodyRef = useRef();
    var expandTime;
    if (bodyRef.current) {
        expandTime = 0.71428571429 * bodyRef.current.clientHeight - 350;
    }

    var [collapsed, setCollapsed] = useState(false);
    useEffect(() => {
        setCollapsed(!expanded && bodyRef.current.clientHeight > 350);
    }, [bodyRef, expanded])
    useEffect(() => {
        bodyRef.current.querySelectorAll("img").forEach((elm) => {elm.addEventListener("click", (ev) => {
            ev.stopPropagation();
        })})
    }, [bodyRef, p.text])

    return (<>
        <div>
            <div className={s.pic}>
                <FallbackImg src={avatar}></FallbackImg>
            </div>
            <Vote up={p.entity.upvotes} down={p.entity.downvotes} id={p.entity.id} my={myVote}></Vote>
        </div>
        <div className={s.content}>
            <div>
                <div className={s.head}>
                    

                    <div
                        className={s.comments}
                        data-state={p.entity.comments_hidden ? "hidden" : p.entity.comments_locked ? "locked" : undefined}
                    >
                        {!p.entity.comments_hidden ?
                            <>{(p.entity.comment.rgt - p.entity.comment.lft - 1) / 2} ‧ {p.entity.comments_locked ? <> {smR?<FaLock size={15}/>:(<b>LOCKED</b>)}</> : <></>}</>
                            :
                            (smR?<FaEyeSlash size={20}/>:(<b>Closed.</b>))
                        }
                        <CommentIcon />
                    </div>

                    <p className={s.title}>
                        <b>{p.title}</b><br />
                        <span className={s.author}>posted by {p.author ? p.author.username : "❌DELETED❌"}</span>
                    </p>
                </div>

            </div>

            <div className={s.body} style={{ maxHeight: !expanded ? "350px" : bodyRef.current.clientHeight + 10, "--expandtime": expandTime + "ms", overflow: "hidden" }}>
                <div dangerouslySetInnerHTML={{ __html: md(text) }} className={mdStyle.mdPost} ref={bodyRef}></div>
                {collapsed && <div className={s.expandDialogWrapper}><div className={s.expandDialog}>Click to Expand</div></div>}
            </div>

            <Timestamp time={p.createdAt} edit={p.updatedAt}></Timestamp>
        </div></>);
}


function later(delay) {
    return new Promise(function (resolve) {
        setTimeout(resolve, delay);
    });
}
function checkVisible(elm) {
    var rect = elm.getBoundingClientRect();
    var viewHeight = Math.max(document.documentElement.clientHeight, window.innerHeight);
    return !(rect.bottom < 0 || rect.top - viewHeight >= 0);
}